.btn {
  position: absolute;
  z-index: 10;
  font-family: 'Open Sans', sans-serif;
  font-weight: 700;
  font-size: 17px;
  text-transform: uppercase;
  color: white;
  width: 180px;
  height: 45px;
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 30px;
  cursor: pointer;
  background: linear-gradient(to right, #854399 0%, #b14599 100%); }
  .btn::before {
    content: '';
    position: absolute;
    z-index: -1;
    width: 180px;
    height: 45px;
    left: 0;
    top: 0;
    background: linear-gradient(to right, #854399 0%, #b14599 100%);
    filter: blur(25px);
    transition: 0.5s; }
  .btn:hover::before {
    content: '';
    transform: scale(0);
    transition: 1s; }

